@import "../0-abstracts/vars";
@import "../0-abstracts/mixins";

.loading-spinner {
  @include display-flex($width: 100vw, $height: 100vh);
  animation: opacity 1s ease-out 2s 1 backwards;
  &__box {
    width: 70px;
    height: 70px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    p {
      @include font($family: "poppins", $color: $black);
      margin-top: 16px;
    }
  }
}


@keyframes opacity{
  0%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}